body {
  /* background-image:url('https://media.blogto.com/articles/20210622-torontosunset1.jpg?w=2048&cmd=resize_then_crop&height=1365&quality=70'); */
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: azure;
}

table {
  width: 100%;
}

div {
  display: block;
}

.main {
  /* padding: 20px; */
}

.section-welcome {
  background-image: radial-gradient(farthest-corner at 40px 40px,
      #f35 0%, #43e 100%);
  background-size: cover;
  height: 95vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.section-about-me {
  background-image: radial-gradient(farthest-corner at 40px 95%,
      #f35 0%, #43e 100%);
  background-size: cover;
  height: 50vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  min-height: 490px;
  padding-top: 30px;
}

.section-projects {
  background-image: radial-gradient(farthest-corner at 40px 40px,
      #f35 0%, #43e 100%);
  background-size: cover;
  padding-top: 50px;
}

.section-experience {
  background-image: radial-gradient(farthest-corner at 40px 95%,
      #f35 0%, #43e 100%);
  background-size: cover;
  padding-top: 50px;
}

.section-education {
  background-image: radial-gradient(farthest-corner at 40px 40px,
      #f35 0%, #43e 100%);
  background-size: cover;
}

.section-extracurriculars {
  background-image: radial-gradient(farthest-corner at 40px 95%,
      #f35 0%, #43e 100%);
  background-size: cover;
  padding-top: 50px;
}

.section-contact {
  background-image: radial-gradient(farthest-corner at 40px 40px,
      #f35 0%, #43e 100%);
  background-size: cover;
  height: 240px;
}

.title {
  padding: 20px;
}

.about-me {
  width: 90%;
}

.introduction {
  width: 60%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.introduction h1 {
  font-size: 3.5em;
}

.introduction h5 {
  display: block;
  font-size: 1.8em;
  font-weight: 500;
  font-family: "Quicksand",sans-serif;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.summary {
  display: flex;
  flex-direction: row;
}

.photo {
  display: flex;
  height: 35vh;
  width: 50%;
  min-height: 300px;
  justify-content: left;
  border-radius: 25px;
}

.photo #profile {
  max-width: 600px;
  offset: top 50px;
  width: 90%;
  height: 100%;
  object-fit: cover;
  border-radius: inherit;
  padding: 5px;
  background: #43e;
}

.my-info-background {
  max-width: 1000px;
  min-width: 900px;
  min-height: 300px;
  height: 35vh;
  padding: 5px;
  background-color: #43e;
  border-radius: 25px;
  width: 100%;
}

.my-info {
  width: 100%;
  height: 100%;
  justify-content: space-evenly;
  border-radius: inherit;
  padding: 15px;
  background-color: rgba(255, 246, 254, 0.945);
  display: flex;
  flex-direction: row;
  text-align: left;
  overflow: hidden;
}

.first {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.second {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.project-spread {
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 50px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.project-item-border {
  padding: 15px;
  border-radius: 25px;
}

.project-item {
  background-color: antiquewhite;
  width: 30vw;
  height: 30vh;
  max-width: 400px;
  max-height: 300px;
  min-width: 300px;
  min-height: 200px;
  border-radius: inherit;
}

.project-item h3 {
  padding: 5px;
  margin-bottom: -10px;
}

.project-item div {
  padding: 10px;
  height: 86%;
}

.project-item img {
  border-radius: 25px;
  width: 100%;
  height: 100%;
}

.experience {
  display: flex;
  width: 100%;
  text-align: left;
}

.experience h1 {
  padding: 20px;
  width: 20vw;
  min-width: 250px;
}

.education-title {
  padding-top: 50px;
}

.experience-spread {
  padding: 20px;
  background-color: rgb(240, 248, 255, .85);
  width: 100%;
}

.experience-item {
  display: flex;
  flex-direction: row;
  padding: 10px;
}

.experience-item #node {
  width: 45px;
  height: 45px;
}

.experience-item #node-tail {
  width: 45px;
  height: 200px;
  padding: 20px;
}

.experience-date {
  min-width: 150px;
}

.experience-point {
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.experience-info {
  padding-left: 25px;
}

.extracurriculars {
  padding: 20px;
}

.extracurricular-spread {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.extracurricular-panel:first-of-type {
  height: 100vh;
  width: 65%;
  padding-right: 5px;
}

.extracurricular-panel:nth-of-type(2) {
  height: 100vh;
  width: 35%;
}

.extracurricular-panel:nth-of-type(3) {
  height: 50vh;
  width: 100%;
  padding-top: 5px;
}

.extracurricular {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

#trading {
  height: 50%;
  padding-bottom: 5px;
}

.extracurricular-panel:first-of-type .extracurricular:nth-of-type(1) {
  background-image: linear-gradient(135deg,rgba(95, 226, 101, 0.8),rgba(12, 85, 57, 0.8)),url('https://www.nasdaq.com/sites/acquia.prod/files/image/29525db076bcc42505a356e55dbe94f38b28530b_getty-stock-market-data.jpg?53434481');
  background-size: cover;
  background-position: 50%;
  height: 100%;
}

.extracurricular-panel:first-of-type .extracurricular:nth-of-type(2) {
  background-image: linear-gradient(135deg,rgba(226, 213, 95, 0.8),rgba(85, 39, 12, 0.8)),url('https://thebusinessofesports.com/wp-content/uploads/2021/12/chess-playing-hand.jpg');
  background-size: cover;
  background-position: 50%;
  height: 50%;
}

.extracurricular-panel:nth-of-type(2) .extracurricular {
  background-image: linear-gradient(135deg,rgba(226,95,176,.8),rgba(85,12,34,.8)),url('https://wallpaper.dog/large/17036904.jpg');
  background-size: cover;
  background-position: 50%;
  height: 100%;
}

.extracurricular-panel:nth-of-type(3) .extracurricular {
  background-image: linear-gradient(135deg,rgba(95, 115, 226, 0.8),rgba(64, 12, 85, 0.8)),url('https://papers.co/wallpaper/papers.co-ne79-coding-screen-digital-art-blue-34-iphone6-plus-wallpaper.jpg');
  background-size: cover;
  background-position: 50%;
  height: 100%;
}

.contact {
  padding: 30px;
}

.contact-spread {
  display: inline-flex;
}

.contact-info {
  display: flex;
  justify-content: center;
  align-content: center;
  margin: 10px 70px;
  width: 50px;
  height: 50px;
}

.contact-info a {
  cursor: pointer;
  width: 100%;
  height: 100%;
  background-color: #f35;
  border-radius: 50%;
}

